<template>
  <div class="show">
    <!-- 依据案例欢迎界面和答题界面布局是相同的所以引入到同一个组件里面 -->
    <div class="content">
      <!-- 背景  -->
      <div class="index_image" v-if="connectComponent == 'home'"></div>
      <!-- 题目界面内容 -->
      <div class="content-topic" v-if="connectComponent == 'question'">
        <div class="content-topic-question">
          {{ problems[itemNum - 1].stem }}
        </div>
        <div class="content-topic-answer">
          <ul>
            <!-- 这边ABCD的显示由chooseType函数实现显示 -->
            <li
              class="item"
              ref="box"
              v-for="(item, index) in problems[itemNum - 1].answer"
              
              @click="checkAnswer(index)"
            >
              <!-- @click="choosed(index)" -->
              <span>{{ item.describe }}</span>
            </li>
          </ul>
        </div>
      </div>
      <!-- 结算界面内容 -->
      <div class="content-score" v-if="connectComponent == 'score'">
        总分为:{{ totleScore }}
      </div>
    </div>
    <footer class="foot">
      <div class="foot-home" v-if="connectComponent == 'home'">
        <!-- 首页按钮点击路由到题目界面 -->
        <router-link to="question" class="btn_home">开始做题</router-link>
      </div>
      <div class="foot-questiontips" v-if="connectComponent == 'question'">
        <!-- 这边判定一下到最后一题的时候显示 -->
        <!-- @click="submitChoose" -->
        <span
          style="font-weight: 400"
          @click="nextQuestion"
          v-if="itemNum < problems.length"
          >下一题</span
        >
        <span style="font-weight: 400" v-else @click="submitAnswer">交 卷</span>
      </div>
    </footer>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  name: "show",
  props: ["connectComponent"],
  created() {
    // 页面在首页的时候初始化一下所有的值
    if (this.fatherComponent == "home") {
      this.initializeData();
    }
  },
  computed: mapState([
    "itemNum",
    "result",
    "problems",
    "rightResult",
    "totleScore",
  ]),
  data() {
    return {
      choosedId: null, // 用于存所选答案索引
    };
  },
  methods: {
    chooseType: (type) => {
      switch (type) {
        case 0:
          return "A";
        case 1:
          return "B";
        case 2:
          return "C";
        case 3:
          return "D";
      }
    },
    checkAnswer(type) {
      if (this.choosedId == null) {
        this.choosedId = type;
        this.addResult(this.choosedId);
        this.$refs.box[this.choosedId].className = "item wrong_answer"
        this.$refs.box[this.rightResult[this.itemNum - 1]].className = "item right_answer"
      }
    },
    nextQuestion() {
      // 当点击下一题时我们提交当前所选答案值
      if (this.choosedId != null) {
        //保存答案, 题目索引加一，跳到下一题
        this.addNumber(this.choosedId);
        this.choosedId = null;
        for (let i = 0; i < this.$refs.box.length; i++) {
          this.$refs.box[i].className = "item"          
        }
      } else {
        alert("还未选择答案");
      }
    },
    ...mapActions(["addResult", "addNumber", "initializeData", "countScore"]),
    submitAnswer() {
      if (this.choosedId != null) {
        // 交卷按钮任然要保存最后一个题答案，并且路由到结算页面
        this.addResult(this.choosedId);
        this.choosedId = null;
        this.countScore();
        this.$router.push("score");
      }
    },
  },
};
</script>
